<template>
    <section id="book-2">
        <div class="bookCover">
            <img src="../../static/book-2-sm.jpg">
        </div>
        <div class="bookInfo">
            <span><h1>Star Wars Aftermath: Life Debt</h1></span>
            <p>The galaxy is changing, and with peace now a possibility, some dare to imagine new beginnings and new destinies. For Han Solo, that means settling his last outstanding debt, by helping Chewbacca liberate the Wookiee's home world of Kashyyyk.</p>
            <p>Meanwhile, Norra Wexley and her band of rebels pursue Admiral Rae Sloane and the remaining Imperial leadership across the galaxy. Sloane, increasingly wary of the mysterious fleet admiral, desperately searches for a means to save the crumbling Empire from oblivion. Even as Imperial forces fight to regain lost ground, Princess Leia and the New Republic seek to broker a lasting peace.</p>
            <p>But the rebel's hunt for Admiral Sloane is cut short after the disappearance of Han Solo and Chewbacca. Desperate to save them, Leia conscripts Norra, Sinjir, Jas, and the rest of their team to find the missing smugglers and help them in their fight for freedom.</p>
            <div class="reviews">
                <p>"Compulsively readable, the kind of caramel-corn book you just keep stuffing in your face until it’s gone."
                    <span> - Tor.com</span>
                </p>
            </div>
            <div class="reviews">
                <p>"Man oh man, this is good stuff. [Life Debt] reveals what Han and Chewie were up to after Return of the Jedi."
                    <span> - io9</span>
                </p>
            </div>
            <div class="reviews">
                <p>"Gripping reading . . . [This novel] hits the ground running."
                    <span> - New York Daily News</span>
                </p>
            </div>
        </div>   
    </section>
</template>

<script>
  export default {
    props: ['mobile'],
    mounted() {
        if(this.mobile < 800) {
            this.$emit('close');
        }
    }
  }
</script>


