<template>
    <section id="book-3">
        <div class="bookCover">
            <img src="../../static/book-3-sm.jpg">
        </div>
        <div class="bookInfo">
            <span><h1>Star Wars Aftermath: Empire's End</h1></span>
            <p>As the final showdown between the New Republic and the Empire draws near, all eyes turn to a once-isolated planet: Jakku.</p>
            <p>The Battle of Endor shattered the Empire, scattering its remaining forces across the galaxy. But the months following the Rebellion's victory have not been easy. The fledgling New Republic has suffered a devastating attack from the Imperial remnant, forcing the new democracy to escalate their hunt for the hidden enemy.</p>
            <p>For her role in the deadly ambush, Grand Admiral Rae Sloane is the most wanted Imperial war criminal—and one-time rebel pilot Norra Wexley, back in service at Leia's urgent request, is leading the hunt. But more than just loyalty to the New Republic drives Norra forward: Her husband was turned into a murderous pawn in Sloane's assassination plot, and now she wants vengeance as much as justice.</p>
            <p>But Sloane, too, is on a furious quest: pursuing the treacherous Gallius Rax to the barren planet Jakku. As the true mastermind behind the Empire's devastating attack, Rax has led the Empire to its defining moment. The cunning strategist has gathered the powerful remnants of the Empire's war machine, preparing to execute the late Emperor Palpatine's final plan.</p>
            <p>As the Imperial fleet orbits Jakku, an armada of Republic fighters closes in to finish what began at Endor. Norra and her crew soar into the heart of an apocalyptic clash that will leave land and sky alike scorched. And the future of the galaxy will finally be decided.</p>
            <div class="reviews">
                <p>"The Star Wars Aftermath trilogy finishes strong even as it raises as many questions as it answers."
                    <span> - Den of Geek</span>
                </p>
            </div>
            <div class="reviews">
                <p>"The final novel in Chuck Wendig's trilogy concludes the "Aftermath" series in suitably exhilarating style."
                    <span> - New York Daily News</span>
                </p>
            </div>
            <div class="reviews">
                <p>"Delivering a tight, focused story that doesn’t let off the throttle the entire way. This book was difficult to put down."
                    <span> - MakingStarWars.net</span>
                </p>
            </div>
        </div>   
    </section>
</template>

<script>
  export default {
    props: ['mobile'],
    mounted() {
        if(this.mobile < 800) {
            this.$emit('close');
        }
    }
  }
</script>


