<template>
  <div class="container" id="app">
    <header>
        <div class="logo">
          <router-link to="/">
            <img src="../static/logo.png"></router-link>
            <h1>The Aftermath<br>Trilogy Books</h1>
          
        </div>
        <div class="hamburger" @click="openMenu = true">
            <div class="bar"></div>
        </div>
        <div class="navbar" v-if="openMenu">
            <ul>
                <li>
                  <router-link active-class="swa" to="book1">Star Wars Aftermath</router-link>
                </li>
                <li>
                  <router-link active-class="swald" to="book2">Aftermath: Life Debt</router-link>
                </li>
                <li>
                  <router-link active-class="swaee" to="book3">Aftermath: Empire's End</router-link>
                </li>
                <li>
                  <router-link active-class="author" to="author">About the Author</router-link>
                </li>
            </ul>
        </div>
    </header>
    <transition name="fade">
      <router-view :mobile="win" @close="closeMenu"></router-view>
    </transition>
    <footer>
      <p>All data and images are sourced from this <a href="http://starwars.wikia.com/wiki/Star_Wars:_The_Aftermath_Trilogy" target="blank">Wikipedia site</a>.</p>
    </footer>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        openMenu: false,
        win: 0
      }
    },
    created() {
      this.getWidth();
    },
    mounted() {
      this.$nextTick(() => {
        window.addEventListener('resize', this.getWidth);
      });
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.getWidth);
    },
    methods: {
      getWidth() {
        this.win = document.documentElement.clientWidth;
        if(this.win > 800) {
          this.openMenu = true;
        } else {
          this.openMenu = false;
        }
      },
      closeMenu() {
        this.openMenu = false;
      }
    }
  }
</script>

<style>
  @import url('./assets/css/styles.css');
</style>



