<template>
  <section id="main">
      <div class="books">
        <span v-if="mobile >= 800">
          <router-link to="book1">
            <img src="../../static/book-1-3d.png">
          </router-link>
          <router-link to="book2">
            <img src="../../static/book-2-3d.png">
          </router-link>
          <router-link to="book3">
            <img src="../../static/book-3-3d.png">
          </router-link>
        </span>
        <span v-else>
          <div id="booksAll">
            <img src="../../static/books-all-sm.png">
          </div>
          
        </span>
      </div>
      <div class="details">
          <h1>The Aftermath Trilogy by <router-link to="author">Chuck Wendig</router-link></h1>
          <p>Star Wars: The Aftermath Trilogy is a trilogy of novels written by author Chuck Wendig. The trilogy, which began publication on September 4, 2015 with the novel Aftermath, tells a canonical story of events that took place after <strong>Star Wars: Episode VI Return of the Jedi.</strong></p>
          <p>The second and third books of the trilogy, <strong>Aftermath: Life Debt</strong> and <strong>Aftermath: Empire's End</strong>, were announced at <strong>New York Comic Con</strong> on October 10, 2015. Shortly after, it was revealed that Life Debt would be released on July 12, 2016, and Empire's End would be released on January 31, 2017, though this was later delayed until February 21, 2017 instead.</p>
      </div>
  </section>
</template>

<script>
  export default {
    props: ['mobile'],
    mounted() {
        if(this.mobile < 800) {
            this.$emit('close');
        }
    }
  }
</script>




