<template>
    <section id="book-author">
        <div class="bookCover">
            <img src="../../static/chuck-wendig.jpg">
        </div>
        <div class="bookInfo">
            <h1>About The Author</h1>
            <p>Chuck Wendig is is a novelist who wrote the 2015 novel Aftermath, the first canon novel set after the events of Star Wars: Episode VI Return of the Jedi, as well as the sequel, 2016's Aftermath: Life Debt and 2017's Aftermath: Empire's End, the final title in Star Wars: The Aftermath Trilogy. He previously wrote the non-Star Wars novels Blackbird and The Blue Blazes, amongst many others.</p>
            <p>Wendig was hired to write Aftermath after posting a tweet saying he wanted to write for Star Wars. In the tweet, on September 4, 2014, Wendig wrote: "Dear Internet: I wanna write a licensed STAR WARS novel. Please make this happen. *squeezes Internet hard enough to make wishes fall out*" The request was successful, and Aftermath was released exactly one year after the tweet, on September 4, 2015.</p>
            <p>Wendig also wrote the comic adaptations of Star Wars: Episode VII The Force Awakens.</p>
        </div>
    </section>
</template>

<script>
  export default {
    props: ['mobile'],
    mounted() {
        if(this.mobile < 800) {
            this.$emit('close');
        }
    }
  }
</script>


